.menu-list {
  min-height: 60px;
  list-style: none;
  margin: 0;
  padding: 0;
  font-family: "PTSans-Narrow", "Arial", sans-serif;
  font-weight: 700;
  font-size: 26px;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-align: center;
  align-items: center;
}
@media (min-width: 768px) {
