    line-height: 55px;
  }
}
.menu-item--not-active a {
  text-decoration: none;
  color: #ffffff;
  border-radius: 20px;
  padding: 0 15px;
  transition: all 0.4s ease;
}
.menu-item--not-active a:hover,
.menu-item--not-active a:focus {
  background-color: rgba(255, 255, 255, 0.15);
  transition: all 0.4s ease;
  outline: none;
}
.menu-item--active a {
  border-radius: 20px;
  padding: 0 15px;
  color: rgba(255, 255, 255, 0.67);
}
@media (max-width: 767px) {
  .menu-list__menu-item {
    margin-bottom: 2px;
    box-shadow: 0 2px 0 rgba(1, 1, 1, 0.2), inset 0 2px 0 rgba(255, 255, 255, 0.2);
  }
}
@media (max-width: 767px) {
  .menu-list__menu-item:last-child {
    margin-bottom: 0;
    box-shadow: inset 0 2px 0 rgba(255, 255, 255, 0.2);
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 80%, rgba(0, 0, 0, 0) 48%, rgba(0, 0, 0, 0.2) 100%);
  }
}
.button {
  display: block;
  text-decoration: none;
  font-family: "PTSans-Narrow-Bold", "Arial", sans-serif;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #0699a6;
  font-size: 24px;
  line-height: 60px;
  text-align: center;
  width: fit-content;
  padding-left: 30px;
  padding-right: 30px;
  height: 60px;
  border-radius: 30px;
  border: none;
  outline: none;
  transition: all 0.4s ease;
}
@media (max-width: 767px) {
  .button {
    height: 40px;
    font-size: 18px;
    line-height: 40px;
    letter-spacing: 1px;
    padding-left: 15px;
    padding-right: 15px;
  }
}
.button:hover {
  background-color: rgba(6, 153, 166, 0.7);
  transition: all 0.4s ease;
  cursor: pointer;
}
.button--up {
  position: fixed;
  background-color: transparent;
  padding: 0;
  border-radius: 0;
  opacity: 0;
  transition: all 0.4s ease;
}
.button--up:hover {
  background-color: transparent;
  transition: all 0.4s ease;
  cursor: pointer;
}
@media (max-width: 767px) {
  .button--up {
    width: 75px;
    height: 75px;
    bottom: 70px;
    right: calc(50% - 180px);
  }
}
@media (min-width: 768px) and (max-width: 1439px) {
  .button--up {
    width: 100px;
    height: 100px;
    bottom: 100px;
    right: calc(50% - 365px);
  }
}
@media (min-width: 1440px) {
  .button--up {
    width: 100px;
    height: 100px;
    bottom: 100px;
    right: calc(50% - 690px);
  }
}
